import AppNavigation from '@nextcloud/vue/dist/Components/AppNavigation';
import AppNavigationItem from '@nextcloud/vue/dist/Components/AppNavigationItem';
import AppNavigationNew from '@nextcloud/vue/dist/Components/AppNavigationNew';
import AppContent from '@nextcloud/vue/dist/Components/AppContent';
import AppNavigationSettings from '@nextcloud/vue/dist/Components/AppNavigationSettings';
import axios from '@nextcloud/axios';
import CounterBubble from '@nextcloud/vue/dist/Components/CounterBubble';
export default {
  name: 'App',
  components: {
    AppNavigation: AppNavigation,
    AppNavigationItem: AppNavigationItem,
    AppNavigationNew: AppNavigationNew,
    AppContent: AppContent,
    AppNavigationSettings: AppNavigationSettings,
    CounterBubble: CounterBubble
  },
  mounted: function mounted() {
    this.loadAllSettings(this);
    this.getNotifications(this);
  },
  data: function data() {
    return {
      setting: {},
      notification: {
        messages: 0,
        connection_requests: 0
      }
    };
  },
  methods: {
    loadAllSettings: function loadAllSettings(vm) {
      axios.get('/index.php/apps/peppolnext/api/v1/setting').then(function (response) {
        vm.setting = response.data;
      }).catch(function (error) {});
    },
    updateSettings: function updateSettings(vm) {
      var payload = {
        body: this.setting
      };
      axios.post('/index.php/apps/peppolnext/api/v1/setting', payload).then(function (response) {}).catch(function (error) {});
    },
    getNotifications: function getNotifications(vm) {
      axios.get('/index.php/apps/peppolnext/api/v1/message/notifications').then(function (response) {
        vm.notification.messages = response.data.messages;
        vm.notification.connection_requests = response.data.connection_requests;
      }).catch(function (error) {});
    }
  }
};