import axios from '@nextcloud/axios';
import Actions from '@nextcloud/vue/dist/Components/Actions';
import ActionButton from '@nextcloud/vue/dist/Components/ActionButton';
export default {
  name: 'ConnectionList',
  components: {
    Actions: Actions,
    ActionButton: ActionButton
  },
  data: function data() {
    return {
      messages: []
    };
  },
  mounted: function mounted() {
    this.getAllNewInvoices(this);
  },
  methods: {
    getAllNewInvoices: function getAllNewInvoices(vm) {
      axios.get('/index.php/apps/peppolnext/api/v1/message/new/1').then(function (response) {
        vm.messages = response.data;
      }).catch();
    },
    setAsRead: function setAsRead(filename) {
      var payload = {
        filename: filename
      };
      var opt = this;
      axios.put('/index.php/apps/peppolnext/api/v1/message', payload).then(function (response) {
        opt.getAllNewInvoices(opt);
      }).catch(function (error) {});
    },
    deleteFile: function deleteFile(filename) {
      var payload = {
        filename: filename
      };
      var opt = this;
      axios.delete('/index.php/apps/peppolnext/api/v1/message', {
        data: payload
      }).then(function (response) {
        opt.getAllNewInvoices(opt);
      }).catch(function (error) {});
    }
  }
};